#youtubeCont {
    background-color: #f0eeeb;
}
.youtube-cont {
    padding-bottom: 200px;
}
.site {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 200px;
}
.site h2 {
    font-size: 40px;
    padding-bottom: 20px;
    flex: 0 0 100%;
    font-family: 'Chosunilbo_myungjo';
}
.site .left {
    flex: 0 0 35%;
}
.site .right {
    font-family: 'Chosunilbo_myungjo';
    font-size: 18px;
    flex: 0 0 60%;
}
.site .right dl {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.site .right dt {
    flex: 0 0 20%;
}
.site .right dd {
    flex: 0 0 80%;
    padding-bottom: 30px;
}

// youtube-search
.youtube-search {
}
.youtube-search ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.youtube-search li {
    flex: 0 0 19%;
    margin-bottom: 10px;
}
.youtube-seacrch-title {
    font-family: 'Chosunilbo_myungjo';
}
.youtube-seacrch-title h2 {
    display: inline-block;
    margin-right: 20px;
    margin-bottom: 20px;
    font-size: 40px;
}
.youtube-seacrch-title input {
    border: 0;
    padding: 10px;
    font-family: 'Chosunilbo_myungjo';
    vertical-align: 5px;
}
.youtube-seacrch-title button {
    border: 0;
    padding: 10px 30px;
    font-family: 'Chosunilbo_myungjo';
    background: #000;
    color: #fff;
    vertical-align: 5px;
    margin-left: 5px;
}
